import React from 'react';

class HelpPopup extends React.Component {  

	constructor(props) {
		super(props);

		this.state = {
			numPages: 7,
			pageNum: 0,
			firstTime: true
		}

		this.nextHelpPage = this.nextHelpPage.bind(this);
		this.prevHelpPage = this.prevHelpPage.bind(this);
	}

	nextHelpPage() {
		this.setState(state => ({
			pageNum: state.pageNum + 1
		}));
	}

	prevHelpPage() {
		this.setState(state => ({
			pageNum: state.pageNum - 1
		}));
	}

	render() {
		let instructions = "";
		let header = "";
		switch(this.state.pageNum) {
			case 0:
				instructions = (
					<div className="instructions" id={"instructions" + this.state.pageNum}>	
						<figure>
							<img id="shovel_button_help" src="./assets/help-tap.png" />
							<figcaption>
								Tap/Click on houses for three actions to save it from collapse.
							</figcaption>
						</figure>
					</div>
				);
				header = "How to Play";
				break;
			case 1:
				instructions = (
				<div className="instructions" id={"instructions" + this.state.pageNum}>						
					<figure>
						<img id="shovel_button_help" src="./assets/Shovel_Button_Example.png" />
						<img id="shovel_house_help" src="./assets/Shovel_House_Example.png" />
						<figcaption>
							Click to remove snow from house. The more snow, the more money you earn!
					</figcaption>
				</figure>
				</div>
				);
				header = "Shovel";
				break;
			case 2:
				instructions = (
				<div className="instructions" id={"instructions" + this.state.pageNum}>
					<figure>
						<img id="build_button_help" src="./assets/Build_Button_Example.png" />
						<img id="build_house_help" src="./assets/Build_House_Example.png" />
						<figcaption>
							<p>Increase the amount of snow the house can hold before collapsing.
							This costs money.</p>
							<p>Build up to 3 times.</p>
						</figcaption>
					</figure>
				</div>
				);
				header = "Build";
				break;
			case 3:
				instructions = (
				<div className="instructions" id={"instructions" + this.state.pageNum}>
					<figure>
						<img id="raise_button_help" src="./assets/Raise_Button_Example.png" />
						<img id="raise_house_help" src="./assets/Raise_House_Example.png" />
						<figcaption>
							Slow down how quickly snow falls on this house by decreasing the roof's angle.
						</figcaption>
					</figure>
				</div>
				);
				header = "Raise";
				break;
			case 4:
				instructions = (
				<div className="instructions" id={"instructions" + this.state.pageNum}>
					<figure><img id="collapse_warning_help" src="./assets/Warning_Collapse_Example.png" /><figcaption>
					A red exclamation means the house is very close to collapsing.</figcaption></figure>
					<figure><img id="health_help" src="./assets/Health_Bar_Example.png" /><figcaption>
					The health bar shows you how close the house is to collapsing. </figcaption></figure>
				</div>
				);
				header = "Health";
				break;
			case 5:
				instructions = (
				<div className="instructions" id={"instructions" + this.state.pageNum}>
					<figure><img id="collapsed_house_help" src="./assets/Collapsed_House_Example.png" /><figcaption>
					If there's too much snow, the house will collapse into a pile of rubble.
					You'll lose a life if a house collapses.</figcaption></figure>
					<figure><img id="lost_life_help" src="./assets/Lost_Life_Example.png" /><figcaption>
					Filled acorns represent the number of lives you have left. You start out with 3 lives.</figcaption></figure>
				</div>
				);
				header = "Lives";
				break;
			case 6:
				instructions = (
					<div className="instructions" id={"instructions" + this.state.pageNum}>
						<figure><img id="play_pause_help" alt="play" src="./assets/play-cute.svg" /><figcaption>
							Play</figcaption></figure>
						<figure><img id="play_pause_help" alt="info" src="./assets/pause-cute.svg"/><figcaption>
							Pause</figcaption></figure>
						<figure><img id="settings_help" alt="gear" src="./assets/settings-cute.svg"/><figcaption>
							Sound &amp; Difficulty</figcaption></figure>
						<figure><img id="info_help" alt="info" src="./assets/help-cute.svg"/><figcaption>
							Instructions</figcaption></figure>
					</div>
				);
				header = "Help & Settings";
				break;
			default:
				break;
		}

		return (
			<div id='help-popup'>  
				<div className='help-popup-content'>
					<div id="help-popup-header">
						{this.state.pageNum > 0 ? <div id="help-popup-prev-arrow"><img id="help-popup-prev-arrow-button" src="./assets/prev-arrow.svg" onClick={this.prevHelpPage} /></div>:""}
						{(this.state.pageNum < this.state.numPages-1) ?<div id="help-popup-next-arrow"><img id="help-popup-next-arrow-button" src="./assets/next-arrow.svg" onClick={this.nextHelpPage} /></div> : ""}
						<div id="help-popup-close"><img id="help-popup-close-button" src="./assets/exit.svg" onClick={this.props.closePopup} /></div>
						<h1>{ header }</h1>
					</div>
					{ instructions }
				</div>  
			</div>  
		);
	}  
}  

export default HelpPopup;